<template>
    <div>
        <!-- Main jumbotron for a primary marketing message or call to action -->

        <div class="jumbotron">
            <div class="container">
                <h1 class="display-3">{{ patchFormTitle }}</h1>
                <p>{{ patchFormDescription }}</p>
            </div>
        </div>

        <body>
            <main role="main">


                <div class="container">
                    <div class="row">
                        <div class="col-md-8 order-md-1">
                            <form class="needs-validation" validate>
                                <div class="col-md-6 mb-3">
                                    <input type="file" class="custom-file-input" id="customFile" />
                                    <label class="custom-file-label" for="customFile">{{chooseFile}}</label>
                                </div>

                                <div class="row">
                                    <div class="col-md-3 mb-3">
                                        <label for="size">{{size}}</label>
                                        <input type="text" class="form-control" id="size" required v-model="inputSize" />
                                    </div>
                                    <div class="col-md-3 mb-3">
                                        <label for="amount">{{amount}}</label>
                                        <input type="number"
                                               class="form-control"
                                               id="amount"
                                               required
                                               v-model="inputAmount" />
                                    </div>
                                    <div class="col-md-4 mb-3">
                                        <label for="deadline">{{deadline}}</label>
                                        <input type="date" class="form-control" id="deadline" required />
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="col-md-6 mb-3">
                                        <label for="fonts">{{fonts}}</label>
                                        <input type="text"
                                               class="form-control"
                                               id="fonts"
                                               value
                                               required
                                               v-model="inputFonts" />
                                        <div class="invalid-feedback">Kérlek add meg a mintában használt fontot!</div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-6 mb-3">
                                        <label for="comment">{{comment}}</label>
                                        <input type="text" class="form-control" id="comment" value v-model="inputComment" />
                                    </div>
                                </div>

                                <div class="row">
                                    <hr class="mb-4" />
                                    <a class="btn btn-primary" @click="reset" type="reset">{{cancelButton}}</a>
                                    <a class="btn btn-primary"
                                       type="submit"
                                       @mouseup="postPost()">{{orderButton}}</a>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </main>
        </body>
    </div>
</template>

 
 <script>
import axios from "axios";
export default {
  name: "PatchFormBody",
  props: {},
  data() {
    return {
      patchFormTitle: "Folt rendelés",
      patchFormDescription:
        "Foltot így meg így tudsz rendelni, ezt meg ezt kell tudni róla.",
      chooseFile: "Válaszd ki a fájlt",
      pattern: "Minta",
      inputPattern: null,
      patternLocation: "Minta helye",
      inputPatternLocation: null,
      size: "Méret (cm)",
      inputSize: null,
      amount: "Mennyiség",
      inputAmount: null,
      deadline: "Határidő",
      inputDeadline: "2019-12-15T08:58:18.176Z",
      fonts: "Mintában használt fontok",
      inputFonts: null,
      comment: "Megjegyzés",
      inputComment: null,
      cancelButton: "Mégse",
      orderButton: "Megrendelem",
      orderTime: "2019-12-11T08:58:18.176Z"
    };
  },
  methods: {
      reset() {
          console.log("patchform reset")
          this.$router.push('/')
      },
    postPost() {
      axios
        .post(`http://localhost:52140/api/Orders`, {
          size: this.inputSize,
          amount: this.inputAmount,
          deadline: this.inputDeadline,
          pattern: null,
          orderComment: this.inputComment,
          orderTime: this.orderTime,
          fonts: this.inputFonts,
          type: 0,
          patternPlace: this.inputPatternLocation
        })
        .then(
            //currentObj.output = response.data; //a response volt eredetileg a fgv paramétere
            console.log("patchForm submit"),
            this.$router.push("/userorder")
        )
        .catch(e => {
          this.errors.push(e);
        });
    }
  }
};
</script>

<style scoped>
</style>